<template>
  <div :class="[$style.stylization, 'featureSection']">
    <vue-grid>
      <vue-grid-row>
        <vue-grid-item fill class="vueGridItem">
          <flip-header/>
        </vue-grid-item>
        <vue-grid-item class="vueGridItem">
          <vue-icon-movie/>
          <vue-panel class="vuePanel">
            <vue-panel-body>
              <h3>
                <video
                  v-if="videoUrl"
                  ref="originalVideo"
                  autoplay autobuffer muted playsinline controls
                >
                  <source :src="videoUrl" type="video/mp4">
                </video>
                <img ref="originalImg" v-if="originalImg" :src="originalImg">
                <content-loader
                  v-if="!videoUrl && !originalImg"
                  :height="180"
                  :width="320"
                  :speed="3"
                  primaryColor="#7966F3"
                  secondaryColor="#A851E2"
                >
                  <rect x="0" y="0" rx="3" ry="3" width="320" height="180"/>
                </content-loader>
              </h3>
              <p>
                <vue-input
                  name="video"
                  id="video"
                  type="file"
                  message="Upload the to be stylized video or photo"
                  @change="handleVideoUpload"
                />
                <vue-button v-if="!originalImg && videoUrl"
                  accent
                  pulse
                  @click="currentFrame"
                  :disable="enableStylize"
                >Select current frame to be stylized</vue-button>
              </p>
            </vue-panel-body>
          </vue-panel>
        </vue-grid-item>

        <vue-grid-item class="vueGridItem">
          <vue-icon-photo-filter/>
          <vue-panel class="vuePanel">
            <vue-panel-body>
              <h3>
                <img ref="styleImg" v-if="styleImg" :src="styleImg">
                <content-loader
                  v-if="!styleImg"
                  :height="180"
                  :width="320"
                  :speed="2"
                  primaryColor="#7966F3"
                  secondaryColor="#A851E2"
                />
              </h3>
              <p>
                <vue-slider v-if="showSettings" :min="0" :max="100" :values="[100]"/>
                <vue-input
                  name="photo"
                  id="photo"
                  type="file"
                  message="Upload the style"
                  @change="handlePhotoUpload"
                />
              </p>
            </vue-panel-body>
          </vue-panel>
        </vue-grid-item>

        <vue-grid-item class="vueGridItem">
          <vue-icon-movie-filter/>
          <vue-panel class="vuePanel">
            <vue-panel-body>
              <h3>
                <img ref="contentImg" v-if="contentImg && !finishedStylizing" :src="contentImg">
                <content-loader
                  v-if="!contentImg && !finishedStylizing"
                  :height="180"
                  :width="320"
                  :speed="1"
                  primaryColor="#7966F3"
                  secondaryColor="#A851E2"
                />
              </h3>
              <p>
                <vue-checkbox
                  v-if="showSettings"
                  name="radio"
                  id="radio"
                  label="Low quality"
                  :checked="radio === 'lowQuality'"
                  radio
                  @click="selectMobileNetStyleModel"
                />
                <br>
                <vue-loader v-if="downloadInceptionModel" primary/>
                <vue-checkbox
                  v-if="showSettings"
                  name="radio2"
                  id="radio2"
                  label="High quality (selecting this will download extra plugin)"
                  :checked="radio === 'highQuality'"
                  radio
                  @click="downloadInceptionStyleModel"
                />
                <vue-button
                  v-if="!finishedStylizing"
                  accent
                  pulse
                  @click="stylize"
                  :disable="enableStylize"
                >Stylize (this could take a while)</vue-button>
                <!--
                <br>
                <vue-button
                  v-if="!finishedStylizing"
                  accent
                  pulse
                  @click="stylize"
                >Stylize current video frame (preview)</vue-button>
                -->
              </p>
              <canvas ref="stylizedResult"></canvas>
            </vue-panel-body>
          </vue-panel>
        </vue-grid-item>
      </vue-grid-row>
    </vue-grid>
  </div>
</template>

<script>
import VueGrid from "../../components/VueGrid/VueGrid.vue";
import VueGridItem from "../../components/VueGridItem/VueGridItem.vue";
import VuePanel from "../../components/VuePanel/VuePanel.vue";
import VuePanelBody from "../../components/VuePanel/VuePanelBody/VuePanelBody.vue";
import VueGridRow from "../../components/VueGridRow/VueGridRow.vue";
// import VueIconSpinner from "../../components/icons/VueIconSpinner/VueIconSpinner.vue";
// import VueIconMobile from "../../components/icons/VueIconMobile/VueIconMobile.vue";
// import VueIconPuzzlePiece from "../../components/icons/VueIconPuzzlePiece/VueIconPuzzlePiece.vue";
import VueButton from "../../components/VueButton/VueButton.vue";
// import VueIconFlash from "../../components/icons/VueIconFlash/VueIconFlash.vue";
import VueIconMovieFilter from "../../components/icons/VueIconMovieFilter/VueIconMovieFilter.vue";
import VueIconMovie from "../../components/icons/VueIconMovie/VueIconMovie.vue";
import VueIconPhotoFilter from "../../components/icons/VueIconPhotoFilter/VueIconPhotoFilter.vue";
import VueInput from "../../components/VueInput/VueInput.vue";
import VueCheckbox from "../../components/VueCheckbox/VueCheckbox.vue";
import VueSlider from "../../components/VueSlider/VueSlider.vue";
import VueLoader from "../../components/VueLoader/VueLoader.vue";
import FlipHeader from "../../components/FlipHeader/FlipHeader.vue";
import { ContentLoader } from "vue-content-loader";
//import ffmpeg from "../../ffmpeg-worker-mp4.js";
import * as tf from "@tensorflow/tfjs";

export default {
  components: {
    VueIconPhotoFilter,
    VueInput,
    VueIconMovie,
    VueIconMovieFilter,
    VueButton,
    //VueIconFlash,
    //VueIconPuzzlePiece,
    //VueIconMobile,
    //VueIconSpinner,
    VueGrid,
    VueGridItem,
    VuePanel,
    VuePanelBody,
    VueGridRow,
    VueCheckbox,
    VueSlider,
    VueLoader,
    ContentLoader
  },
  data() {
    return {
      videoUrl: null,
      styleImg: null,
      resultUrl: null,
      styleNet: null,
      mobileStyleNet: null,
      inceptionStyleNet: null,
      transformNet: null,
      enableStylize: false,
      contentImg: null,
      radio: "lowQuality",
      downloadInceptionModel: false,
      showSettings: false,
      finishedStylizing: false,
      originalImg: null
    };
  },
  methods: {
    selectMobileNetStyleModel() {
      this.radio = "lowQuality";
      this.loadMobileNetStyleModel.then(model => {
        this.styleNet = model;
      });
    },
    downloadInceptionStyleModel() {
      this.downloadInceptionModel = true;
      this.radio = "highQuality";
      this.loadInceptionStyleModel()
        .then(model => {
          this.styleNet = model;
        })
        .finally(() => (this.downloadInceptionModel = false));
    },
    async loadMobileNetStyleModel() {
      if (!this.mobileStyleNet) {
        this.mobileStyleNet = await tf.loadFrozenModel(
          "saved_model_style_js/tensorflowjs_model.pb",
          "saved_model_style_js/weights_manifest.json"
        );
      }

      return this.mobileStyleNet;
    },

    async loadInceptionStyleModel() {
      if (!this.inceptionStyleNet) {
        this.inceptionStyleNet = await tf.loadFrozenModel(
          "saved_model_style_inception_js/tensorflowjs_model.pb",
          "saved_model_style_inception_js/weights_manifest.json"
        );
      }

      return this.inceptionStyleNet;
    },
    handleVideoUpload(e) {
      const file = e[0];
      if (file["type"].includes("video")) {
        this.videoUrl = URL.createObjectURL(file);
      } else if (file["type"].includes("image")) {
        this.originalImg = URL.createObjectURL(file);
      }
      this.finishedStylizing = false;
      if (this.styleImg) this.showSettings = true;
    },
    handlePhotoUpload(e) {
      const file = e[0];
      //this.styleImg = URL.createObjectURL(file); //AND THE REST THAT COMES WITH READASDATAURL
      const fileReader = new FileReader();
      this.finishedStylizing = false;
      fileReader.onload = e => {
        this.styleImg = e.target.result;
      };
      fileReader.readAsDataURL(file);
      if (this.videoUrl) this.showSettings = true;
    },
    currentFrame() {
      const canvas = document.createElement("canvas");
      canvas.width = this.$refs.originalVideo.videoWidth;
      canvas.height = this.$refs.originalVideo.videoHeight;

      canvas.getContext("2d").drawImage(this.$refs.originalVideo, 0, 0);

      const dataUri = canvas.toDataURL("image/jpeg");
      const data = dataUri.split(",")[1];

      this.contentImg = URL.createObjectURL(
        new window.Blob([Buffer.from(data, "base64")])
      );
    },
    stylize() {
      if (this.$refs.originalVideo || this.originalImg) {
        /*
        this.convertURIToImageData(this.contentImg)
          .then(function(contentData) {
            this.convertURIToImageData(this.styleImg)
              .then(function(styleData) {
                // Here you can use imageData

                this.startStyling(contentData, styleData);
              })
              .catch(error => { console.log(error) });
          })
          .catch(error => { console.log(error) });
          */
        this.startStyling();
        /*
          var time = new Date(this.$refs.originalVideo.currentTime * 1000).toISOString().substr(11, 8)
          var reader = new FileReader();

          reader.onload = function(e) {
            var arrayBuffer = reader.result;
            var result = ffmpeg({
              MEMFS: [{name: "test.mp4", data: arrayBuffer}],
              arguments: ["-i", "test.mp4", "-ss", time, "-vframes", "1", 'out1.jpeg'],
              // Ignore stdin read requests.
              stdin: function() {},
            });
            console.log(result)
            // Write out.webm to disk.
            var out = result.MEMFS[0];
            //fs.writeFileSync(out.name, Buffer(out.data));
            this.resultUrl = URL.createObjectURL(out)
            // ffmpeg -i test.mp4 -ss 00:01:14.35 -vframes 1 out2.png
          }

          reader.readAsArrayBuffer(this.videoFile);
          // Encode test video to VP8.
          */
      } else {
        console.log("Please select a video!");
      }
    },
    convertURIToImageData(URI) {
      return new Promise(function(resolve, reject) {
        if (URI == null) return reject();
        var canvas = document.createElement("canvas"),
          context = canvas.getContext("2d"),
          image = new Image();
        image.addEventListener(
          "load",
          function() {
            canvas.width = image.width;
            canvas.height = image.height;
            context.drawImage(image, 0, 0, canvas.width, canvas.height);
            resolve(context.getImageData(0, 0, canvas.width, canvas.height));
          },
          false
        );
        image.src = URI;
      });
    },
    async startStyling() {
      /*
      const styleNet = await tf.loadFrozenModel(
        "saved_model_style_js/tensorflowjs_model.pb",
        "saved_model_style_js/weights_manifest.json"
      );
      const transformNet = await tf.loadFrozenModel(
        "saved_model_transformer_js/tensorflowjs_model.pb",
        "saved_model_transformer_js/weights_manifest.json"
      );
      console.log("Loaded styleNet");
      this.styleNet = styleNet;
      this.transformNet = transformNet;
      this.enableStylize = true;
      */

      await tf.nextFrame();
      // this.$refs.styleImg.height = 300;
      // this.$refs.styleImg.width = 300;
      // this.$refs.contentImg.height = 300;
      // this.$refs.contentImg.width = 300;
      console.log("Generating 100D style representation");
      await tf.nextFrame();
      let bottleneck = await tf.tidy(() => {
        return this.styleNet.predict(
          tf
            .fromPixels(this.$refs.styleImg)
            .toFloat()
            .div(tf.scalar(255))
            .expandDims()
        );
      });
      // this.styleButton.textContent = "Stylizing image...";
      await tf.nextFrame();
      let stylized = await tf.tidy(() => {
        return this.transformNet
          .predict([
            tf
              .fromPixels(this.$refs.contentImg || this.$refs.originalImg)
              .toFloat()
              .div(tf.scalar(255))
              .expandDims(),
            bottleneck
          ])
          .squeeze();
      });
      this.finishedStylizing = true;
      await tf.toPixels(stylized, this.$refs.stylizedResult);
      bottleneck.dispose(); // Might wanna keep this around
      stylized.dispose();
      /*
      await tf.nextFrame();
      bottleneck = await tf.tidy(() => {
        return this.styleNet.predict(
          tf
            .fromPixels(this.$refs.styleImg)
            .toFloat()
            .div(tf.scalar(255))
            .expandDims()
        );
      });
      // this.styleButton.textContent = "Stylizing image...";
      await tf.nextFrame();
      stylized = await tf.tidy(() => {
        return this.transformNet
          .predict([
            tf
              .fromPixels(this.$refs.contentImg || this.$refs.originalImg)
              .toFloat()
              .div(tf.scalar(255))
              .expandDims(),
            bottleneck
          ])
          .squeeze();
      });
      this.finishedStylizing = true;
      await tf.toPixels(stylized, this.$refs.stylizedResult);
      bottleneck.dispose(); // Might wanna keep this around
      stylized.dispose();
      */
    }
  },
  mounted() {
    Promise.all([
      tf.loadFrozenModel(
        "saved_model_style_js/tensorflowjs_model.pb",
        "saved_model_style_js/weights_manifest.json"
      ),
      tf.loadFrozenModel(
        "saved_model_transformer_js/tensorflowjs_model.pb",
        "saved_model_transformer_js/weights_manifest.json"
      )
    ])
      .then(([styleNet, transformNet]) => {
        console.log("Loaded mobileStyleNet");
        this.styleNet = styleNet;
        this.transformNet = transformNet;
        this.enableStylize = true;
      })
      .catch(error => {
        console.log(error);
      });
    /*
    var stdout = "";
    var stderr = "";
    // Print FFmpeg's version.
    ffmpeg({
      arguments: ["-version"],
      print: function(data) {
        stdout += data + "\n";
      },
      printErr: function(data) {
        stderr += data + "\n";
      },
      onExit: function(code) {
        console.log("Process exited with code " + code);
        console.log(stdout);
      }
    });
    */
  }
};
</script>

<style lang="scss" module>
@import "../../styles";

.stylization {
  display: block;
  text-align: center;

  i {
    display: inline-block;
    height: $font-size-h1;
    width: $font-size-h1;
    margin-top: $space-unit;
  }
}
.convert {
  font-size: $font-size-h1;
  top: $space-unit * 17;
  box-shadow: $nav-bar-shadow;
  display: inline-block;
  background: $nav-bar-bg;
  transition: $button-transition;
  transition-property: box-shadow, background-color;
  color: $text-color;
  padding: $button-padding;

  &:hover {
    box-shadow: $button-active-shadow;
  }

  @include media(tabletPortrait) {
    top: $space-unit * 30;
  }

  @include media(tabletLandscape) {
    top: $space-unit * 42;
  }

  i {
    height: $font-size-h1;
    width: $font-size-h1;
  }
  .title,
  .subTitle,
  .convert {
    text-shadow: 0 5px 10px rgba(0, 0, 0, 0.33);
    position: relative;
  }
}
</style>
