<template>
  <div class="app_header">
    <h2>
      Create
      <strong>
        <div v-bind:class="{app_header_list: true, vue: highlight}">
          <span v-bind:class="{react: true, active: !highlight}">React</span>
          <span v-bind:class="{vue: true, active: highlight}">Vue</span>
        </div>Content Loader
      </strong>
    </h2>
  </div>
</template>

<script>
export default {
  components: {},
  data() {
    return {
      highlight: 0,
      loader: true
    };
  },
  methods: {
    timer() {
      const highlight = this.highlight;
      this.highlight = highlight === 1 ? 0 : highlight + 1;
    }
  },
  componentDidMount() {
    const intervalId = setInterval(this.timer, 3000);
    setTimeout(() => {
      loader = false;
    }, 3000);
  }
};
</script>

<style lang="scss" module>
@import "../../styles";

.app_header {
  grid-column: 1/3;
  text-align: center;
  margin-bottom: 3em;
  h1 {
    font-size: 1.6em;
    font-weight: 400;
    color: #a5a5a5;
    strong {
      color: #637cc3;
    }
  }
}

.app_header_list {
  display: inline-block;
  width: 2.6em;
  height: 1em;
  overflow: hidden;
  position: relative;
  -webkit-transition: width 0.7s ease;
  -o-transition: width 0.7s ease;
  transition: width 0.7s ease;
  &.vue {
    width: 1.75em;
  }
  span {
    display: block;
    position: absolute;
    left: 0;
    top: 0;
    opacity: 0;
  }
  .vue {
